:root {
    --color-primary: #00f0ff;
    --color-secondary: #6c5ce7;
    --color-accent: #2dd4bf;
    --color-background: #0a0f24;
    --color-surface: #151a2e;
    --color-border: #2a2f45;
    --color-text: #e0e0e0;
    --color-text-light: #a0a0a0;
    --color-success: #4caf50;
    --color-error: #ff5252;
    --color-warning: #ffc107;
    --color-info: #2196f3;
    --color-ship-hover: rgba(0, 240, 255, 0.3);
    --color-ship-invalid: #ff525280;
    --color-hit: #ff5252;
    --color-miss: #2196f3;
    --color-sunk: #ff5252;
    --color-board-background: #151a2e;
    --color-board-border: #2a2f45;
    --color-overlay: rgba(0, 240, 255, 0.1);
    --color-dark-overlay: rgba(0, 0, 0, 0.85);
    --scrollbar-track: #151a2e;
    --scrollbar-thumb: #2a2f45;
    --scrollbar-thumb-hover: #3b4055;

    --ship-filter: invert(50%) sepia(34%) saturate(353%) hue-rotate(192deg) brightness(99%) contrast(90%);
}

/* Reset */

*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    -webkit-font-smoothing: antialiased;
    line-height: 1.5;
}

img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}

input,
button,
textarea,
select,
dialog,
form,
label {
    font: inherit;
    color: inherit;
    outline: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}

p {
    text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    text-wrap: balance;
}

#root,
#__next {
    isolation: isolate;
}

ul,
li {
    list-style-type: none;
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

button {
    all: unset;
}

button,
label[for],
select,
option,
::-webkit-search-cancel-button,
::-webkit-scrollbar-thumb,
::-webkit-scrollbar-track {
    cursor: pointer;
}

/* General */

.container {
    width: 95%;
    margin: 0 auto;
    max-width: 1200px;
    border: 2px solid var(--color-border);
    border-radius: 10px;
}

.neon-snake {
    position: absolute;
    width: 16px;
    height: 16px;
    background: var(--neon-color);
    border-radius: 50%;
    box-shadow:
        0 0 10px var(--neon-color),
        0 0 20px var(--neon-color);
    animation: snake-move 4s linear infinite;
}

.row {
    display: flex;
}

body {
    min-height: 100svh;
    display: grid;
    place-items: center;
    background-color: var(--color-background);
    font-family: "Segoe UI", system-ui, sans-serif;
    color: var(--color-text);
}

/* Main menu */

.main {
    background-color: var(--color-surface);
    display: flex;
    flex-direction: column;
    min-width: 80%;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.menu__main {
    align-items: center;
    justify-content: space-evenly;
    gap: 1em;
    padding: 1em 0;
    min-height: 95%;
    overflow-x: hidden;
}

#title {
    text-align: center;
    width: 100%;
    letter-spacing: 1.2rem;
    font-weight: 900;
    font-size: 3rem;
    color: var(--color-text);
    animation: glow 2s ease-in-out infinite alternate;
    transform-style: preserve-3d;
    position: relative;
    transform: perspective(800px) rotateX(25deg);
    transform-origin: top center;
}

#title::after {
    content: "";
    position: absolute;
    top: 0;
    left: -75%;
    width: 50%;
    height: 100%;
    background: linear-gradient(120deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.1));
    transform: skewX(-30deg);
    animation: sheen 2.5s forwards;
    pointer-events: none;
    opacity: 0;
}

/* Player Containers */

.player-containers {
    width: 100%;
    justify-content: space-evenly;
    gap: 1em;
    padding: 0 1em;
}

.player-container {
    position: relative;
    display: grid;
    grid-auto-flow: row;
    justify-content: space-evenly;
    gap: 1.5em;
    align-items: center;
    justify-items: center;
    background-color: var(--color-surface);
    border: 2px solid var(--color-border);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
    padding: 1.5em 0em;
    border-radius: 1em;
}

.game-board.dark-overlay::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: black;
    animation: shrink 0.5s ease-in-out;
}

.player-container__upper-row {
    display: grid;
    grid-template-columns: 5fr 1fr;
    width: 75%;
    align-items: center;
}

.dropdown-container {
    gap: 1em;
}

label {
    display: flex;
    align-items: center;
}

select {
    padding: 0.3em 0.6em;
    border-radius: 8px;
    background: var(--color-surface);
    border: 1px solid var(--color-border);
    margin: 0 auto;
}

.visibility-toggle {
    justify-self: end;
    transition: transform 0.3s ease;
    cursor: auto;
}

.visibility-toggle.can-click {
    cursor: pointer;
}

.visibility-toggle.can-click:hover {
    transform: scale(1.1);
}

#startBtn {
    padding: 0.6rem 5rem;
    background: var(--color-primary);
    color: var(--color-background);
    border-radius: 8px;
    font-weight: bold;
    font-size: 1.2em;
    transition:
        transform 0.3s ease,
        box-shadow 0.3s ease;
    box-shadow: 0 4px 16px rgba(0, 240, 255, 0.3);
}

#startBtn:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 24px rgba(0, 240, 255, 0.5);
}

/* Ship list */

.ship-list {
    display: flex;
    justify-content: space-between;
    overflow-x: auto;
    border: 1px solid var(--color-border);
    background-color: var(--color-surface);
    width: 75%;
    max-width: 900px;
    min-height: 6.5rem;
}

.ship-list__item {
    display: flex;
    flex-direction: column;
    padding: 1em 1em;
    align-items: center;
    transition: transform 0.2s ease;
    min-width: 7rem;
}

.ship-list__item:hover {
    transform: translateY(-3px);
}

.img-container {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ship__img,
.board__ship {
    filter: var(--ship-filter);
}

.ship__img[draggable="true"] {
    width: 4rem;
    transition: filter 0.3s ease;
}

.ship__img[draggable="true"]:hover {
    cursor: grab;
}

.ship__img[draggable="true"]:active {
    cursor: grabbing;
}

[data-ship="battleship"] {
    aspect-ratio: 299 / 95;
}

[data-ship="carrier"] {
    aspect-ratio: 302 / 98;
}

[data-ship="cruiser"] {
    aspect-ratio: 153 / 69;
}

[data-ship="destroyer"] {
    aspect-ratio: 1.87 / 1;
}

[data-ship="submarine"] {
    aspect-ratio: 302 / 98;
    transform: translateY(15.5%); /* the svg is a little wonky */
}

.ship-list::-webkit-scrollbar {
    height: 5px;
}

.ship-list::-webkit-scrollbar-track {
    background: var(--scrollbar-track);
}

.ship-list::-webkit-scrollbar-thumb {
    background: var(--scrollbar-thumb);
}

.ship-list::-webkit-scrollbar-thumb:hover {
    background: var(--scrollbar-thumb-hover);
}

/* Board */

.game-board {
    position: relative;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr);
    background-color: var(--color-board-background);
    aspect-ratio: 1;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
    border: 2px solid var(--color-border);
    border-radius: 8px;
    transform: scale(1);
    transition:
        transform 0.4s ease-in-out,
        box-shadow,
        0.4s ease-in-out;
    animation: none;
}

.menu__main .game-board:hover {
    box-shadow: 0 0 20px rgba(0, 240, 255, 0.1);
}

.menu__main .game-board {
    width: min(70%, 350px);
}

.cell {
    border: 1px solid var(--color-border);
    transition: all 0.2s ease;
}

.cell.ship__hover {
    background-color: var(--color-ship-hover);
}

.cell.invalid {
    background-color: var(--color-ship-invalid);
}

.board__ship-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 10%;
    pointer-events: none;
    &.vertical {
        transform-origin: top left;
        transform: rotate(90deg) translateY(-100%);
    }
}

.board__ship {
    width: 90%;
    height: 95%;
}

/* Button list */

.button-list {
    width: 75%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0.4em;
}

.menu-button {
    flex: 1;
    font-size: 0.9rem;
    text-align: center;
    padding: 0.2em 0.4em;
    background-color: var(--color-board-background);
    border-radius: 8px;
    border: 1px solid var(--color-border);
    transition: all 0.3s ease;
    flex-wrap: wrap;
}

.menu-button.active {
    background-color: var(--color-primary);
    color: var(--color-background);
    box-shadow: 0 0 12px var(--color-primary);
}

.menu-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(0, 240, 255, 0.2);
}

/* Game */

.game-main {
    justify-content: center;
    min-height: 80vh;
}

.board-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
}

.game-main .game-board {
    min-width: 40%;
    overflow: hidden; /* For the glowing cells */
}

.game-board.current-board {
    position: relative;
    overflow: hidden;
    transform: scale(1.05);
    box-shadow: 0 0 20px rgba(0, 240, 255, 0.1);
}

.pass-device-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    padding: 0.6rem 1.5rem;
    background-color: var(--color-primary);
    color: var(--color-background);
    border: 2px solid var(--color-border);
    border-radius: 1rem;
    font-size: 1rem;
    width: 22ch;
    text-align: center;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    box-shadow: 0 0 20px rgba(0, 240, 255, 0.4);
    transition: all 0.3s ease-in-out;
    animation:
        fadeInUp 1s ease-out forwards,
        glow 1s ease-in-out infinite alternate;
}

.pass-device-button:hover {
    transform: translate(-50%, -50%) scale(1.05);
    box-shadow: 0 0 30px rgba(0, 240, 255, 0.6);
    color: var(--color-text);
}

/* Cells */

.miss {
    background-color: var(--color-miss);
    position: relative;
    overflow: hidden;
}

.cell.miss::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 150%;
    height: 150%;
    background: rgba(255, 255, 255, 0.5);
    transform: translate(-50%, -50%);
    border-radius: 50%;
    animation: rippleEffect 1s ease-out forwards;
}

.hit {
    background-color: var(--color-hit);
    animation:
        pulseHit 0.4s ease,
        shake 0.4s ease,
        infiniteGlow 3s ease-in-out 0.8s infinite;
}

.sunk {
    background-color: var(--color-sunk);
}

.boom {
    animation:
        sink 0.4s ease-in-out,
        shake 0.4s ease 0.4s forwards,
        infiniteGlow 3s ease-in-out 0.8s infinite;
}

.cell.hover:hover {
    cursor: pointer;
    background-color: var(--color-accent);
}

/**** Animations ****/

/* Text */
@keyframes glow {
    from {
        text-shadow: 0 0 10px rgba(0, 240, 255, 0.4);
    }
    to {
        text-shadow: 0 0 20px rgba(0, 240, 255, 0.8);
    }
}

@keyframes sheen {
    0% {
        left: -75%;
        opacity: 0;
    }
    25% {
        opacity: 1;
    }
    50% {
        left: 50%;
        opacity: 1;
    }
    100% {
        left: 125%;
        opacity: 0;
    }
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translate(-50%, -60%);
    }
    to {
        opacity: 1;
        transform: translate(-50%, -50%);
    }
}

/* Cells */
@keyframes pulseHit {
    0% {
        transform: scale(1);
    }
    70% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes sink {
    from {
        transform: scale(3);
        box-shadow: 0 0 20px rgba(255, 0, 0, 1);
    }
    to {
        transform: scale(1);
        box-shadow: none;
    }
}

@keyframes shake {
    0% {
        transform: translateX(0);
    }
    25% {
        transform: translateX(-4px);
    }
    50% {
        transform: translateX(4px);
    }
    75% {
        transform: translateX(-4px);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes infiniteGlow {
    0% {
        box-shadow: 0 0 10px rgba(255, 0, 0, 0.5);
    }
    50% {
        box-shadow: 0 0 30px rgba(255, 0, 0, 1);
    }
    100% {
        box-shadow: 0 0 10px rgba(255, 0, 0, 0.5);
    }
}

@keyframes rippleEffect {
    0% {
        transform: scale(0);
        opacity: 1;
    }
    100% {
        transform: scale(4);
        opacity: 0;
    }
}

/* dark overlay */
@keyframes shrink {
    from {
        transform: scale(1.1);
    }

    scale {
        transform: scale(1);
    }
}

/* Media Queries */

@media (max-height: 850px) and (min-width: 1080px) {
    .player-container {
        grid-template-rows: auto 12fr auto auto;
        grid-template-columns: 3fr 1fr;
        gap: 0;
        row-gap: 1em;
        column-gap: 1em;
        padding: 1em 2em;
    }

    .player-container__upper-row {
        grid-row: 1 / 2;
        grid-column: 1 / -1;
    }

    .ship-list {
        grid-row: 2 / -1;
        grid-column: 2;
        width: 100%;
        min-width: 12ch;
        height: 90%;
        flex-direction: column;
        overflow-y: auto;
    }

    .ship-list::-webkit-scrollbar {
        height: 0px;
        width: 5px;
    }

    .menu__main .game-board {
        grid-row: 2 / 3;
        grid-column: 1 / 2;
        width: 100%;
    }

    .button-list {
        grid-row: 3;
        grid-column: 1 / 2;
        width: 100%;
    }

    .menu-button {
        padding: 0.2em 0;
    }

    .player-container__upper-row {
        width: 100%;
    }

    .dropdown-container {
        margin: 0 auto;
    }
}

@media (max-width: 1080px) {
    .menu__main {
        margin: 2em 0;
    }

    .dropdown-container {
        margin: 0 auto;
    }

    .player-containers {
        flex-direction: column;
        align-items: center;
    }

    .player-container {
        width: 90%;
        max-width: 500px;
    }

    .menu__main .game-board {
        width: 80%;
    }

    .ship-list,
    .button-list {
        width: 90%;
    }
}

@media (max-width: 786px) {
    .game-main {
        margin: 2em 0;
    }

    .board-container {
        flex-direction: column;
        padding: 3rem 0;
        gap: 3em;
    }

    .game-main {
        min-height: 95%;
    }

    .game-main .game-board {
        width: 80%;
    }

    .pass-device-button {
        font-size: 0.7rem;
        padding: 0.5rem 1rem;
    }

    #startBtn {
        padding: 0.3em 2.5em;
    }

    .player-containers {
        margin: 1.2em 0;
    }
}

@media (max-width: 580px) {
    #title {
        letter-spacing: 8px;
        font-size: 2.5rem;
    }

    .button-list {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(12ch, 1fr));
        gap: 0.4em;
    }

    .dropdown-container {
        flex-direction: column;
        margin: 0;
    }

    .player-containers {
        padding: 0;
    }

    select {
        margin: 0;
        width: 80%;
    }

    .board-container {
        padding: 0.5em 0;
    }

    .game-main {
        justify-content: space-evenly;
    }
}

@media (max-width: 480px) {
    #title {
        font-size: 1.8rem;
    }
}
