;;IAEA;QACI,UAAU;IACd;;IAEA;QACI,iBAAiB;QACjB,oBAAoB;IACxB;;IAEA;QACI,oBAAoB;IACxB;;IAEA;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,mBAAmB;QACnB,iBAAiB;IACrB;;IAEA;QACI,aAAa;QACb,0DAA0D;QAC1D,UAAU;IACd;;IAEA;QACI,sBAAsB;QACtB,SAAS;IACb;;IAEA;QACI,UAAU;IACd;;IAEA;QACI,SAAS;QACT,UAAU;IACd;;IAEA;QACI,gBAAgB;IACpB;;IAEA;QACI,6BAA6B;IACjC;AACJ;;AAEA;IACI;QACI,iBAAiB;IACrB;AACJ","sourcesContent":["/* Colors */\n\n:root {\n    --color-primary: #00f0ff;\n    --color-secondary: #6c5ce7;\n    --color-accent: #2dd4bf;\n    --color-background: #0a0f24;\n    --color-surface: #151a2e;\n    --color-border: #2a2f45;\n    --color-text: #e0e0e0;\n    --color-text-light: #a0a0a0;\n    --color-success: #4caf50;\n    --color-error: #ff5252;\n    --color-warning: #ffc107;\n    --color-info: #2196f3;\n    --color-ship-hover: rgba(0, 240, 255, 0.3);\n    --color-ship-invalid: #ff525280;\n    --color-hit: #ff5252;\n    --color-miss: #2196f3;\n    --color-sunk: #ff5252;\n    --color-board-background: #151a2e;\n    --color-board-border: #2a2f45;\n    --color-overlay: rgba(0, 240, 255, 0.1);\n    --color-dark-overlay: rgba(0, 0, 0, 0.85);\n    --scrollbar-track: #151a2e;\n    --scrollbar-thumb: #2a2f45;\n    --scrollbar-thumb-hover: #3b4055;\n\n    --ship-filter: invert(50%) sepia(34%) saturate(353%) hue-rotate(192deg) brightness(99%) contrast(90%);\n}\n\n/* Reset */\n\n*,\n*::before,\n*::after {\n    padding: 0;\n    margin: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    -webkit-font-smoothing: antialiased;\n    line-height: 1.5;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n    display: block;\n    max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect,\ndialog,\nform,\nlabel {\n    font: inherit;\n    color: inherit;\n    outline: none;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    overflow-wrap: break-word;\n}\n\np {\n    text-wrap: pretty;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    text-wrap: balance;\n}\n\n#root,\n#__next {\n    isolation: isolate;\n}\n\nul,\nli {\n    list-style-type: none;\n    list-style: none;\n}\n\na {\n    text-decoration: none;\n    color: inherit;\n}\n\nbutton {\n    all: unset;\n}\n\nbutton,\nlabel[for],\nselect,\noption,\n::-webkit-search-cancel-button,\n::-webkit-scrollbar-thumb,\n::-webkit-scrollbar-track {\n    cursor: pointer;\n}\n\n/* General */\n\n.container {\n    width: 95%;\n    margin: 0 auto;\n    max-width: 1200px;\n    border: 2px solid var(--color-border);\n    border-radius: 10px;\n}\n\n.neon-snake {\n    position: absolute;\n    width: 16px;\n    height: 16px;\n    background: var(--neon-color);\n    border-radius: 50%;\n    box-shadow:\n        0 0 10px var(--neon-color),\n        0 0 20px var(--neon-color);\n    animation: snake-move 4s linear infinite;\n}\n\n.row {\n    display: flex;\n}\n\nbody {\n    min-height: 100svh;\n    display: grid;\n    place-items: center;\n    background-color: var(--color-background);\n    font-family: \"Segoe UI\", system-ui, sans-serif;\n    color: var(--color-text);\n}\n\n/* Main menu */\n\n.main {\n    background-color: var(--color-surface);\n    display: flex;\n    flex-direction: column;\n    min-width: 80%;\n    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);\n}\n\n.menu__main {\n    align-items: center;\n    justify-content: space-evenly;\n    gap: 1em;\n    padding: 1em 0;\n    min-height: 95%;\n    overflow-x: hidden;\n}\n\n#title {\n    text-align: center;\n    width: 100%;\n    letter-spacing: 1.2rem;\n    font-weight: 900;\n    font-size: 3rem;\n    color: var(--color-text);\n    animation: glow 2s ease-in-out infinite alternate;\n    transform-style: preserve-3d;\n    position: relative;\n    transform: perspective(800px) rotateX(25deg);\n    transform-origin: top center;\n}\n\n#title::after {\n    content: \"\";\n    position: absolute;\n    top: 0;\n    left: -75%;\n    width: 50%;\n    height: 100%;\n    background: linear-gradient(120deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.1));\n    transform: skewX(-30deg);\n    animation: sheen 2.5s forwards;\n    pointer-events: none;\n    opacity: 0;\n}\n\n/* Player Containers */\n\n.player-containers {\n    width: 100%;\n    justify-content: space-evenly;\n    gap: 1em;\n    padding: 0 1em;\n}\n\n.player-container {\n    position: relative;\n    display: grid;\n    grid-auto-flow: row;\n    justify-content: space-evenly;\n    gap: 1.5em;\n    align-items: center;\n    justify-items: center;\n    background-color: var(--color-surface);\n    border: 2px solid var(--color-border);\n    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);\n    padding: 1.5em 0em;\n    border-radius: 1em;\n}\n\n.game-board.dark-overlay::after {\n    content: \"\";\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n    background-color: black;\n    animation: shrink 0.5s ease-in-out;\n}\n\n.player-container__upper-row {\n    display: grid;\n    grid-template-columns: 5fr 1fr;\n    width: 75%;\n    align-items: center;\n}\n\n.dropdown-container {\n    gap: 1em;\n}\n\nlabel {\n    display: flex;\n    align-items: center;\n}\n\nselect {\n    padding: 0.3em 0.6em;\n    border-radius: 8px;\n    background: var(--color-surface);\n    border: 1px solid var(--color-border);\n    margin: 0 auto;\n}\n\n.visibility-toggle {\n    justify-self: end;\n    transition: transform 0.3s ease;\n    cursor: auto;\n}\n\n.visibility-toggle.can-click {\n    cursor: pointer;\n}\n\n.visibility-toggle.can-click:hover {\n    transform: scale(1.1);\n}\n\n#startBtn {\n    padding: 0.6rem 5rem;\n    background: var(--color-primary);\n    color: var(--color-background);\n    border-radius: 8px;\n    font-weight: bold;\n    font-size: 1.2em;\n    transition:\n        transform 0.3s ease,\n        box-shadow 0.3s ease;\n    box-shadow: 0 4px 16px rgba(0, 240, 255, 0.3);\n}\n\n#startBtn:hover {\n    transform: translateY(-2px);\n    box-shadow: 0 6px 24px rgba(0, 240, 255, 0.5);\n}\n\n/* Ship list */\n\n.ship-list {\n    display: flex;\n    justify-content: space-between;\n    overflow-x: auto;\n    border: 1px solid var(--color-border);\n    background-color: var(--color-surface);\n    width: 75%;\n    max-width: 900px;\n    min-height: 6.5rem;\n}\n\n.ship-list__item {\n    display: flex;\n    flex-direction: column;\n    padding: 1em 1em;\n    align-items: center;\n    transition: transform 0.2s ease;\n    min-width: 7rem;\n}\n\n.ship-list__item:hover {\n    transform: translateY(-3px);\n}\n\n.img-container {\n    height: 100%;\n    width: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.ship__img,\n.board__ship {\n    filter: var(--ship-filter);\n}\n\n.ship__img[draggable=\"true\"] {\n    width: 4rem;\n    transition: filter 0.3s ease;\n}\n\n.ship__img[draggable=\"true\"]:hover {\n    cursor: grab;\n}\n\n.ship__img[draggable=\"true\"]:active {\n    cursor: grabbing;\n}\n\n[data-ship=\"battleship\"] {\n    aspect-ratio: 299 / 95;\n}\n\n[data-ship=\"carrier\"] {\n    aspect-ratio: 302 / 98;\n}\n\n[data-ship=\"cruiser\"] {\n    aspect-ratio: 153 / 69;\n}\n\n[data-ship=\"destroyer\"] {\n    aspect-ratio: 1.87 / 1;\n}\n\n[data-ship=\"submarine\"] {\n    aspect-ratio: 302 / 98;\n    transform: translateY(15.5%); /* the svg is a little wonky */\n}\n\n.ship-list::-webkit-scrollbar {\n    height: 5px;\n}\n\n.ship-list::-webkit-scrollbar-track {\n    background: var(--scrollbar-track);\n}\n\n.ship-list::-webkit-scrollbar-thumb {\n    background: var(--scrollbar-thumb);\n}\n\n.ship-list::-webkit-scrollbar-thumb:hover {\n    background: var(--scrollbar-thumb-hover);\n}\n\n/* Board */\n\n.game-board {\n    position: relative;\n    display: grid;\n    grid-template-columns: repeat(10, 1fr);\n    grid-template-rows: repeat(10, 1fr);\n    background-color: var(--color-board-background);\n    aspect-ratio: 1;\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);\n    border: 2px solid var(--color-border);\n    border-radius: 8px;\n    transform: scale(1);\n    transition:\n        transform 0.4s ease-in-out,\n        bo